import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import logo from '../logo.svg'
import styled from 'styled-components'
import Button from '../components/Button'
import { ProductConsumer } from '../context'
export default class Navbar extends Component {
    render() {
        return (
            <ProductConsumer>
                {(value) => {
                    const cart = value.cart;
                    let counter = 0;
                    counter = cart.length > 0?cart.map(item => {return item.count}).reduce((a,b) => a + b):0;

                return (
                <NavWrapper className="navbar navbar-expand-sm navbar-dark px-sm-5">
                {/* 
                https://www.iconfinder.com/icons/1243689/call_phone_icon
                Creative Commons (Attribution 3.0 Unported);
                https://www.iconfinder.com/Makoto_msk  */
                }              
                <Link to='/'>
                    <img src={logo} alt="store" className="navbar-brand"/>
                </Link>
                <ul className="navbar-nav align-items-center">
                    <li className="nav-item ml-5">
                        <Link to="/" className="nav-link">
                            Products
                        </Link>
                    </li>
                </ul>
                <Link to="/cart" className="ml-auto">
                    <Button 
                    color="var(--mainWhite)" 
                    bg="var(--lightBlue)"
                    hoverColor="var(--mainBlue)"
                    hoverBg="var(--lightBlue)">
                       <span className="mr-2"><i className="fas fa-cart-plus"></i></span>
                        <span className="mr-5">My Cart</span>
            <span className="cart-counter">{counter}</span>
                    </Button>
                </Link>
            </NavWrapper>
                )
                }}
              
            </ProductConsumer>
        )
    }
}

const NavWrapper = styled.nav `
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;
    background: var(--mainBlue);
    height: 65px;
    .nav-link {
        color: var(--mainWhite) !important;
        font-size: 1.3rem;
        text-transform: capitalize;
    }
    button {
        position: relative;
    }
    .cart-counter {
        position: absolute;
        bottom: 0;
        right: 0;
        font-size: 1.1rem;
        background-color: var(--mainPink);
        border-top: 1px solid var(--mainWhite);
        border-left: 1px solid var(--mainWhite);
        border-radius: 0.7rem 0 0.5rem 0;
        padding: .05rem .7rem;
        
    }
`