import React, { Component } from 'react'
import {storeProducts, detailProduct} from './data'
const ProductContext = React.createContext();
// Provider

// Consumer
class ProductProvider extends Component {
    state = {
        products: [],
        detailProduct,
        cart: [],
        modalOpen: false,
        modalProduct: detailProduct,
        cartSubtotal: 0,
        cartTax: 0,
        cartTotal: 0,
    }
    componentDidMount() {
        this.setProducts();
    }
    setProducts = () => {
        let products = [];
        storeProducts.forEach(item => {
            const singleItem = {...item};
            products = [...products, singleItem];
        })
        this.setState(() => {
            return {products}
        })
    }

    getItem = (id) =>  {
        const product = this.state.products.find(item => item.id === id);
        return product;
    }
    handleDetail = (id) => {
        const product = this.getItem(id);
        this.setState(() => {
            return {detailProduct: product}
        })
    }
    addToCart = (id) => {
        let tempProducts = [...this.state.products];
        const index = tempProducts.indexOf(this.getItem(id));
        const product = tempProducts[index]
        product.inCart = true;
        product.count = 1;
        const price = product.price;
        product.total = price;
        this.setState(() => {
            return {
                products: tempProducts,
                cart: [...this.state.cart, product],   
            }
        }, () => {this.addTotals()})
    }
    openModal = (id) => {
        const product = this.getItem(id);
        this.setState(() => {
            return {
                modalProduct: product,
                modalOpen: true,
            }
        })
    }
    closeModal = () => {
        this.setState(() => {
            return {
                modalOpen: false,
            }
        })
    }
    increment = (id) => {
        let tempProducts = this.state.products;
        const item = this.getItem(id);
        const index = tempProducts.indexOf(item);
        let count = item.count;
        count += 1;
        tempProducts[index].count = count;
        this.setState(() => {
            return {
                products: tempProducts
            }
        }, () => {this.addTotals()})
    }
    decrement = (id) => {
        let products = this.state.products;
        const item = this.getItem(id);
        const index = products.indexOf(item);
        if (item.count == 1) {
            return null
        }
        let count = item.count;
        count -= 1;
        products[index].count = count;
        this.setState(() => {
            return {
                products,
            }
        }, () => {this.addTotals()})
    }
    removeItem = (id) => {
        let cart = this.state.cart;
        let cartItem = cart.find(item => item.id == id);
        let cartIndex = cart.indexOf(cartItem);
        let products = this.state.products;
        const productsItem = this.getItem(id);
        const productsIndex = products.indexOf(productsItem)
        products[productsIndex].inCart = false;
        cart.splice(cartIndex, 1);
        this.setState(() => {
            return {
                cart,
                products,
            }
        }, () => {this.addTotals()})
        
    }
    clearCart = () => {
        let products = this.state.products;
        products.map(item => item.inCart = false)
        this.setState(() => {
            return {
                cart: [],
                products,
            }
        })
    }
    addTotals = () => {
        let subTotal = 0;
        this.state.cart.map(item => (subTotal += item.total * item.count));
        let tempTax = subTotal * 0.1;
        let tax = parseFloat(tempTax.toFixed(2));
        const total = subTotal + tempTax;
        this.setState(() => {
            return {
                cartSubtotal: subTotal,
                cartTax: tax,
                cartTotal: total,
            }
        })
    }
    render() {
        return (
            <ProductContext.Provider value={{
                ...this.state,
                handleDetail: this.handleDetail,
                addToCart: this.addToCart,
                openModal: this.openModal,
                closeModal: this.closeModal,
                increment: this.increment,
                decrement: this.decrement,
                removeItem: this.removeItem,
                clearCart: this.clearCart,
            }}>
                {this.props.children}
            </ProductContext.Provider>
        )
    }
}

const ProductConsumer = ProductContext.Consumer;

export { ProductProvider, ProductConsumer }