import React, { Component } from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {ProductConsumer} from '../context'
import PropTypes from 'prop-types'
export default class Product extends Component {
    render() {
        const {id, title, img, price, inCart} = this.props.product
        return (
            <ProductWrapper className="col-9 mx-auto col-md-6 col-lg-3 mb-5">
                <div className="card">
                    <ProductConsumer>
                        {(value) => (
                           <div onClick={() => 
                           value.handleDetail(id)} className="img-container p-5">
                          <Link to="/details">
                         <img src={img} alt={title} className="card-img-top" />
                             </Link>
                                 <button className="cart-btn" disabled={inCart?true:false} onClick={() => {
                                     value.addToCart(id);
                                     value.openModal(id);
                                     }}>
                                 {inCart?<p className="text-capitalize mb-0" disabled>item already in cart</p>:<i className="fas fa-cart-plus"></i>}
                                </button>
                             </div>
                        )}

                    </ProductConsumer>



                    {/* card footer */}
                    <div className="card-footer d-flex justify-content-between">
                        <p className="align-self-center mb-0">{title}</p>
                        <h5 className="text-blue font-italic mb-0">
                            <span className="mr-1">$</span>
                            {price}
                        </h5>
                    </div>
                </div>
            </ProductWrapper>
        )
    }
}

Product.propTypes = {
    product: PropTypes.shape({
        id: PropTypes.number,
        img: PropTypes.string,
        title: PropTypes.string,
        price: PropTypes.number,
        inCard: PropTypes.bool,
    }).isRequired
}

const ProductWrapper = styled.section `
    .card {
        border-color: transparent;
        transition: all .5s linear;
    }
    .card-footer {
        background: var(--mainPink);
        border-top: transparent;
        transition: all .5s linear;
    }
    &:hover{
        .card {
            border: 0.04rem solid rgba(0,0,0,.2);
            box-shadow: 2px 2px 5px 0px rgba(0,0,0,.2)
        }
        .card-footer {
            background: rgba(247, 247, 247)
        }
    }
    .img-container {
        position: relative;
        overflow: hidden;
    }
    .card-img-top {
        transition: all .5s linear
    }
    .img-container:hover .card-img-top {
        transform: scale(1.2);
    }
    .cart-btn {
        position: absolute;
        bottom: 0;
        right: 0;
        border: 1px solid transparent;
        border-radius: 0.7rem 0 0 0;
        background: var(--mainPink);
        padding: .2rem .4rem;
        color: var(--mainWhite);
        font-size: 1.2rem;
        transform: translate(100%, 100%);
        transition: .5s all linear;
        transition: color background-color .3s linear;
        &:focus {
            outline: none;
        }
    }
    .img-container:hover .cart-btn {
        transform: translate(0, 0);
    }
    .cart-btn:hover {
        color: var(--mainPink);
        background-color: #fff;
        border-left: 1px solid var(--mainPink);
        border-top: 1px solid var(--mainPink);
        cursor: pointer;
    }
`