import React, { Component } from 'react'
import styled from 'styled-components'
import {Link} from 'react-router-dom'
import {ProductConsumer} from '../context'
import Button from './Button'
export default class Modal extends Component {
    render() {
        return (
            <ProductConsumer>
                {(value) => {
                    const {modalOpen, closeModal} = value;
                    const {img, title, price} = value.modalProduct;

                    // if (!modalOpen) {
                    //     return null
                    // }
                    // else {
                        return (<ModalContainer>
                            <div>
                                <div className={'modal modalOpen-' + modalOpen}>
                                    <div className="row">
                                        <div id="modal" className="col-8 mx-auto p-5 text-center text-capitalize">
                                            <h5>Item added to the cart</h5>
                                            <img src={img} alt={title} className="img-fluid"/>
                                            <h5>{title}</h5>
                                            <h5 className="text-muted">price: ${price}</h5>
                                            <Link to='/'><Button onClick={() => closeModal()}>Go to Store</Button></Link>
                                            <Link to='/cart'>
                                                <Button 
                                                hoverBg="var(--mainYellow)"
                                                hoverColor="var(--mainDark)"
                                                color="var(--mainYellow)" 
                                                onClick={() => closeModal()}>Go To cart</Button></Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </ModalContainer>
                        )
                    }
                }
            </ProductConsumer>
        )
    }
}

const ModalContainer = styled.div `
    .modal {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: -1;
    }
    .modalOpen-false {
        #modal {
            transform: scale(0);
            z-index: -2;
            visibility: hidden;

        }
    }
    .modalOpen-true {
        background: rgba(0,0,0,.5);
        z-index: 2;
        #modal {
            background: var(--mainWhite);
            border-radius: 10px;
            transform: scale(1);
            transition: .4s ease;
            transform: scale(1);
        }
    }

`