import React, { Component } from 'react'
import {ProductConsumer} from '../context'
import {Link} from 'react-router-dom'
import Button from './Button'
export default class Details extends Component {
    render() {
        return (
            <ProductConsumer>
                {(value) => {
                    const {id, company, title, img, info, price, inCart} = value.detailProduct;
                    return (
                        <div className="container py-5">
                            {/* title */}
                                <div className="row">
                                    <div className="col-10 mx-auto text-center text-slanted text-blue my-5">
                                        <h1>{title}</h1>
                                    </div>
                                </div>
                            {/* end title */}
                            {/* Product info */}
                                <div className="row">
                                    <div className="col-10 mx-auto col-md-6 my-3">
                                        <img src={img} className="img-fluid" alt="product"/>
                                    </div>
                                    {/* product text */}
                                    <div className="col-10 mx-auto col-md-6 my-3">
                                        <h2>model: {title}</h2>
                                    <h4 className="text-title text-uppercase text-muted mt-3 mb-2">made by : <span className="text-uppercase">{company}</span></h4>
                                    <h4 className="text-blue"><strong>price: <span className="mr-1s">$</span>{price}</strong></h4>
                                    <p className="text-capitalize font-weight-bold mt-3 mb-0">
                                        some info about product:
                                    </p>
                                    <p className="text-muted lead">{info}</p>

                                    {/* buttons*/}
                                        <Link to="/">
                                            <Button color="var(--lightBlue)" bg="var(--lightBlue)" hoverColor="var(--mainWhite)">back to products</Button>
                                        </Link>

                                        <Button 
                                        onClick={() => {
                                            value.addToCart(id);
                                            value.openModal(id);
                                        }}
                                        color="var(--mainYellow)"
                                        disabled={inCart?true:false} 
                                        hoverColor="var(--mainDark)"
                                        hoverBg="var(--mainYellow)">
                                            {inCart?'inCart':'add to cart'}
                                        </Button>
                                    {/* buttons end */}
                                    </div>
                                </div>
                            {/* end product info */}
                        </div>
                    )
                }}
            </ProductConsumer>
        )
    }
}
