import styled from 'styled-components'
import React, { Component } from 'react'




export default class Button extends Component {
        constructor(props) {
            const {onClick, disabled, color = "var(--lightBlue)", bg = "transparent", text, hoverColor = "var(--mainBlue)", hoverBg="var(--lightBlue)"} = props;
            super(props);
            this.state = {
                color,
                backgroundColor: bg,
                text,
                hoverColor,
                hoverBg,
                disabled,
                onClick,
            }
        }
    
        render() {
            const ButtonContainer = styled.button `
                text-transform: capitalize;
                border: 1px solid ${this.state.color};
                color: ${this.state.color};
                border-radius: .5rem;
                padding: 0.2rem 0.5rem;
                font-size: 1.4rem;
                background: transparent;
                cursor: pointer;
                margin: .2rem .5rem .2rem 0;
                transition: all .5s ease-in-out;
                &:hover {
                    background: ${this.state.hoverBg};
                    color: ${this.state.hoverColor};
                    border: 1px solid ${this.state.backgroundColor};
                }
                &:focus {
                    outline: none;
                }
            `
        return (
            <ButtonContainer onClick={this.state.onClick} disabled={this.state.disabled}>{this.props.children}</ButtonContainer>
        )
    }
}




